<template>
	<div class="v-knowledge-single" v-loading="loading">
		<div class="m-navigation">
			<el-button class="u-goback" size="medium" icon="el-icon-arrow-left" @click="goBack" plain>返回列表</el-button>
			<!-- <Fav class="u-collect" post-type="knowledge" :post-id="id" /> -->
		</div>

		<div class="m-wiki" v-if="data && data.post">
			<WikiPanel class="m-knowledge-panel" :wiki-post="data">
				<template slot="head-title">
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 412.72 412.72" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M404.72 82.944h-27.2v-9.28a8 8 0 00-5.76-8 225.345 225.345 0 00-57.68-7.36c-32 0-75.6 7.2-107.84 40-32-33.12-75.92-40-107.84-40a225.345 225.345 0 00-57.68 7.36 8 8 0 00-5.76 8v9.2H8a8 8 0 00-8 8v255.52a8 8 0 008 8 7.996 7.996 0 003.92-1.04c.8-.4 80.8-44.16 192.48-16h1.92a8.008 8.008 0 001.92 0c112-28.4 192 15.28 192.48 16a8 8 0 0012-6.88V90.944a8 8 0 00-8-8zM16 333.664V98.944h19.12v200.64a8 8 0 009.2 8 350.096 350.096 0 0150-4 207.516 207.516 0 0168.32 10.32A294.1 294.1 0 0016 333.664zm78.32-46a351.994 351.994 0 00-43.52 2.8V79.984a220.645 220.645 0 0147.44-5.28c29.92 0 71.2 6.88 99.84 39.2l.24 199.28c-16.64-10.88-49.12-25.52-104-25.52zm120-173.76c28.64-32 69.92-39.2 99.84-39.2a221.61 221.61 0 0147.44 5.28v210.48a351.895 351.895 0 00-43.28-2.88c-54.56 0-87.12 14.64-104 25.52v-199.2zm182.32 219.76a294.158 294.158 0 00-146.96-19.76 208.008 208.008 0 0168.64-10.32 349.994 349.994 0 0150.32 3.92 8 8 0 009.2-8V98.944h19.12l-.32 234.72z"/></svg>
					<span>{{ data.source.label }} - {{ data.post.title }} </span>
				</template>
				<template slot="head-actions">
					<a class="el-button el-button--primary" :href="publishLink(`knowledge/${id}`)">
						<i class="el-icon-edit"></i>
						<span>完善百科通识</span>
					</a>
				</template>
				<template slot="body">
					<Article :content="data.post.content" />
					<Thx class="m-thx" slot="single-append" :postId="id" postType="knowledge" :userId="author_id" :adminBoxcoinEnable="isRevision" :userBoxcoinEnable="isRevision" mode="wiki" />
				</template>
			</WikiPanel>

			<WikiRevisions v-if="id" type="knowledge" :source-id="id" style="margin-bottom: 35px" />

			<template v-if="id">
				<el-divider content-position="left">讨论</el-divider>
				<Comment :id="id" category="knowledge" />
			</template>
		</div>

		<div v-else class="m-wiki-null">
			<i class="el-icon-s-opportunity"></i>
			<span>暂无内容，我要</span>
			<a class="s-link" :href="publishLink(`knowledge/${id}`)">完善百科通识</a>
		</div>
	</div>
</template>

<script>
import { getKnowledgeDetail, getKnowledgePost } from "@/service/knowledge.js";
import { postStat } from "@jx3box/jx3box-common/js/stat";
import { publishLink } from "@jx3box/jx3box-common/js/utils";
import WikiPanel from "@jx3box/jx3box-common-ui/src/wiki/WikiPanel";
import WikiRevisions from "@jx3box/jx3box-common-ui/src/wiki/WikiRevisions";

import Article from "@jx3box/jx3box-editor/src/Article.vue";
import Comment from "@jx3box/jx3box-comment-ui/src/Comment.vue";

export default {
	name: "Detail",
	data: function () {
		return {
			loading: false,
			data: "",
		};
	},
	components: {
		Article,
		WikiPanel,
		WikiRevisions,
		Comment,
	},
	computed: {
		id: function () {
			return this.$route.params.source_id;
		},
		type: function () {
			return this.data.source.type;
		},
		isRevision: function () {
			return !!this.$route.params.post_id;
		},
		author_id: function () {
			return ~~this.data.post.user_id;
		},
	},
	methods: {
		getData() {
			this.loading = true;
			getKnowledgeDetail(this.id)
				.then((res) => {
					this.data = res;
					if (this.data.source) this.data.source.post = this.data.post;
				})
				.finally(() => {
					this.loading = false;
					postStat(this.type, this.id);
				});
		},
		getPostData() {
			this.loading = true;
			getKnowledgePost(this.$route.params.post_id)
				.then((res) => { 
					this.data = res;
					if (this.data.source) this.data.source.post = this.data.post;
				})
				.finally(() => {
					this.loading = false;
					postStat(this.type, this.id);
				});
		},
		goBack() {
			this.$router.push({ name: "normal", params: { knowledge_type: this.data.source.type } });
		},
		publishLink,
	},
	created: function () {
		this.getData(this.id);
	},
	watch: {
		"$route.params.post_id": {
			immediate: true,
			handler() {
				if (this.$route.params.post_id) {
					// 获取指定攻略
					this.getPostData();
				}
			},
		},
	},
};
</script>

